.film-item-size {
  flex-basis: 100%;

  @include media-from-tablet-resolution {
    flex-basis: calc((100% - 30px) / 2);
  }

  @include media-from-desktop-resolution {
    flex-basis: calc((100% - 30px) / 3);
  }
}



a.tui-page-btn.tui-next-is-ellip.tui-last-child,
a.tui-page-btn.tui-prev-is-ellip.tui-first-child,
a.tui-page-btn.tui-last,
a.tui-page-btn.tui-first{
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
}

.tui-ico-ellipsis, .tui-ico-first, .tui-ico-last, .tui-ico-next, .tui-ico-prev {
  overflow: hidden;
}

.tui-next, .tui-prev {
  background-color: #f7f7f7;
  border-radius: 5px;
}

.gallery-for-movies{
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.box-movies{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

@include media-from-tablet-resolution {
  a.tui-page-btn.tui-next-is-ellip.tui-last-child,
  a.tui-page-btn.tui-prev-is-ellip.tui-first-child,
  a.tui-page-btn.tui-last,
  a.tui-page-btn.tui-first{
    position: relative;
    overflow: visible;
  }

  .box-movies {
    gap: 30px;
  }

}

@include media-from-desktop-resolution {
  .box-movies {
    gap: 30px 15px;
    justify-content: start;
  }
}

.tui-pagination {
  display: flex;
  align-self: center;
  gap: 5px;
  height: 40px;
  margin: 40px 0 40px;
  line-height: normal;
  text-align: center;
  font-size: 0;
  padding-left: 20px;
  padding-right: 20px;
}

.tui-pagination .tui-page-btn {
  font-style: normal;
  font-weight: 500;
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
  padding: 13px;
  margin-left: 0;
  color: #000000;
  font-size: 12px;
  line-height: 1.33;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  background-position: 0 -40px;
  transition:background-position 0.5s ;
}

.tui-pagination .tui-page-btn:hover {
  background-image:linear-gradient(#bdbbb9,#cbc9c7) ;
  border-radius: 5px;
  background-repeat: no-repeat;
  background-position: 0 0;
}

.tui-pagination .tui-is-selected, .tui-pagination strong {
  color: #fff;
  background-color:#FF6B08;
  z-index: 9;
  border-color: #FF6B08;
  cursor: default;
  border-radius: 5px;
}

.tui-pagination .tui-first-child.tui-is-selected, .tui-pagination strong.tui-first-child {
  margin-left: 0
}

.tui-pagination .tui-prev, .tui-pagination .tui-next,.tui-pagination .tui-first, .tui-pagination .tui-last, .tui-pagination .tui-prev-is-ellip, .tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  height: 40px;
  width: 40px;
  padding: 13px;
  font-size: 0;
  white-space: nowrap;
  font-weight: normal;
}

.tui-pagination .tui-ico-first, .tui-pagination .tui-ico-prev, .tui-pagination .tui-ico-next, .tui-pagination .tui-ico-last, .tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,) no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}

.tui-pagination .tui-prev-is-ellip, .tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0 0
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 20px;
  margin-top: 18px;
  vertical-align: top;
  background-position: 0px -24px
}

.tui-pagination .tui-is-disabled:hover {
  background: #fff;
  cursor: default
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;

}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: #4b96e6
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top
}