@import "/node_modules/modern-normalize/modern-normalize.css";

h1,
h2,
h3,
h4,
p {
  margin-top: 0;
  margin-bottom: 0;
}

ul,
ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

body {
  font-family: var(--primary-font);
  color: var(--primary-text-color-theme-light);
  display: flex;
  flex-direction: column;
}

html, body {
  height: 100%;
}

#content {
  flex-grow: 2;
}